angular
  .module('app', []);

angular
  .module('app')
  .component('app', {
    templateUrl: 'app/hello.html',
    controller: function ($http, $log) {
      var main = this;
          main.titles = [];

          main.searchQuery = '';

          main.clearSearch = function() {
            main.searchQuery = '';
          };

          main.showQR = function(title) {
            var qrCode = kjua({
              render: 'image',
              crisp: true,
              minVersion: 1,
              ecLevel: 'H',
              size: 240,
              ratio: null,
              fill: '#333',
              back: '#fff',
              text: encodeURI(title.url),
              rounded: 80,
              quiet: 0,
              mode: 'plain'
            });

            $('#qr-' + title.titleid).empty().append(qrCode);
          };

      $http.get('https://api.titledb.com/v0/')
        .error(function(err) {
          $log.error(err);
        })
        .success(function(data) {
          main.titles = data;
        });
    },
    controllerAs: 'main'
  });

angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/hello.html","<div class=\"section\">\n  <div class=\"row\">\n    <div class=\"col m10 offset-m1 s12\">\n\n      <div class=\"row\" ng-hide=\"main.titles.length\">\n        <div class=\"col l12 m12 s12 center-align\">\n          <div class=\"preloader-wrapper big active\">\n            <div class=\"spinner-layer spinner-blue-only\">\n              <div class=\"circle-clipper left\">\n                <div class=\"circle\"></div>\n              </div><div class=\"gap-patch\">\n              <div class=\"circle\"></div>\n            </div><div class=\"circle-clipper right\">\n              <div class=\"circle\"></div>\n            </div>\n            </div>\n          </div>\n        </div>\n      </div>\n\n      <div class=\"row\" ng-show=\"main.titles.length\">\n\n        <div class=\"col l12 m12 s12\">\n          <form>\n            <div class=\"input-field\">\n              <i class=\"material-icons left\" id=\"search-label\">search</i>\n              <input id=\"search\" type=\"search\" ng-model=\"main.searchQuery\" placeholder=\"Search\" required>\n              <i class=\"material-icons right\" id=\"search-clear\" ng-click=\"main.clearSearch()\">close</i>\n            </div>\n          </form>\n        </div>\n\n        <div class=\"col l3 m6 s12\" ng-repeat=\"title in main.titles | orderBy:\'name\' | filter:main.searchQuery\">\n          <div class=\"card hoverable hb-card\">\n            <div class=\"card-image waves-effect waves-block waves-light\">\n              <img class=\"activator\" ng-src=\"https://api.titledb.com/images/{{ title.titleid }}.png\" ng-click=\"main.showQR(title)\">\n            </div>\n            <div class=\"card-content\">\n              <div class=\"row\">\n                <img id=\"icon-{{ title.titleid }}\" class=\"right\" ng-src=\"https://api.titledb.com/images/{{ title.titleid }}.png\">\n                <h6 class=\"grey-text text-darken-4 truncate\">{{ title.name }}</h6>\n                <p class=\"grey-text truncate\">{{ title.author }}</p>\n                <p class=\"truncate\">{{ title.description }}</p>\n              </div>\n              <div class=\"row no-margin\">\n                <button class=\"col l6 m6 s6 btn-flat activator waves-effect\" ng-click=\"main.showQR(title)\">QR</button>\n                <a href=\"{{ title.url }}\" class=\"col l6 m6 s6 btn-flat waves-effect\">Download</a>\n              </div>\n            </div>\n            <div class=\"card-reveal\">\n              <div class=\"row no-margin\">\n                <span class=\"card-title grey-text text-darken-4\">\n                <span><i class=\"material-icons right close-reveal\">close</i></span>\n                </span>\n              </div>\n              <div class=\"qr-frame\">\n                <div id=\"qr-{{ title.titleid }}\"></div>\n              </div>\n            </div>\n          </div>\n        </div>\n\n      </div>\n\n    </div>\n  </div>\n</div>\n");}]);